<template lang="pug">
.page-my-car
    my-nav-bar(title="我的爱车")
    van-cell-group
        van-field(label="车牌号" placeholder="请输入车牌号" v-model="form.car_no")
        my-form-car-model-picker(label="车辆型号" placeholder="请选择" :data="carModels" v-model="form.car_id")
    my-bottom-bar.bottom(:click="save")
        .g-bottom-btn 提交
</template>

<script>
export default {
    data () {
        return {
            form: {
                car_no: '',
                car_id: 0
            },
            carModels: []
        };
    },
    methods: {
        async getAllCarModel () {
            try {
                let { data } = await this.$http.post('/admin/car/cars', { p: 1 });
                console.log(data);
                this.carModels = data;
            } catch (error) {
                console.log(error);
            }
        },
        async getMyCar () {
            try {
                let { data } = await this.$http.post('/mobile/user/car', { p: 1 });
                console.log(data);
                Object.assign(this.form, data);
            } catch (error) {
                console.log(error);
            }
        },
        async save () {
            try {
                await this.$http.post('/mobile/user/carsave', this.form);
                this.$dialog.alert({
                    message: '保存成功'
                });
                history.back();
            } catch (error) {
                console.log(error);
            }
        }
    },
    created () {
        this.getAllCarModel();
        this.getMyCar();
    }
};
</script>



